body{
    font-family: 'Josefin Sans', sans-serif;
}
.projects{
    min-height: 100vh;
    background-color: rgb(53, 114, 19);
}
.projects__title{
    text-align: center;
    font-size: 22px;
    padding-top: 25px;
}
.container{
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}


.projects__items{
    display: flex;
    justify-content: space-around;
    margin-top: 170px;
}