.Contacts_container__1XKM8::after{
    content: '';
    position: absolute;
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
    background: linear-gradient(45deg, #ff0047, #6eff00);
    pointer-events: none;
    -webkit-animation: Contacts_animate__2b31T 10s linear infinite;
            animation: Contacts_animate__2b31T 10s linear infinite;
}

@-webkit-keyframes Contacts_animate__2b31T {
    0% {
        -webkit-filter: blur(60px) hue-rotate(0deg);
                filter: blur(60px) hue-rotate(0deg);
    }
    100% {
        -webkit-filter: blur(60px) hue-rotate(360deg);
                filter: blur(60px) hue-rotate(360deg);
    }
}

@keyframes Contacts_animate__2b31T {
    0% {
        -webkit-filter: blur(60px) hue-rotate(0deg);
                filter: blur(60px) hue-rotate(0deg);
